import React from "react";
import { HeadFC, PageProps, graphql } from "gatsby";

import { IPostQueryResult } from "../types/post.type";

import { LocalBusinessMetadata } from "../components/metadata";
import ContentContainer from "../components/contentContainer";
import Banner from "../components/banner";

const Post = ({
  data: {
    markdownRemark: { frontmatter, html },
  },
}: PageProps<IPostQueryResult>) => {
  const { date, author, title } = frontmatter;

  return (
    <>
      <Banner
        heightClass="h-96"
        title="CodeCave blog"
        text="Awesome blog provides important information for you"
      />
      <div itemScope itemType="https://schema.org/Article">
        <ContentContainer className="flex-col">
          <div className="flex justify-between p-8">
            <p className={`text-xl text-main-10`} itemProp="datePublished">
              {date}
            </p>
            <p
              className={`text-xl text-main-10`}
              itemProp="author"
              itemScope
              itemType="https://schema.org/Person"
            >
              <span itemProp="name">{author}</span>
            </p>
          </div>
          <h2
            className="text-main-100 text-4xl font-bold pb-8"
            itemProp="headline"
          >
            {title}
          </h2>
          <div
            className="single-post text-main-70 text-2xl pb-10"
            dangerouslySetInnerHTML={{ __html: html }}
            itemProp="articleBody"
          />
        </ContentContainer>
        <LocalBusinessMetadata itemProp="copyrightHolder" />
      </div>
    </>
  );
};

export default Post;

export const Head: HeadFC<IPostQueryResult> = ({
  data: {
    markdownRemark: {
      frontmatter: { title },
    },
  },
}) => <title>CodeCave blog: {title}</title>;

export const query = graphql`
  query ($id: String) {
    markdownRemark(id: { eq: $id }) {
      frontmatter {
        author
        date(formatString: "MMM DD, YYYY")
        title
      }
      html
    }
  }
`;
